.header {
  background: rgba(0, 0, 0, 0.56);
  padding-top: 0px;
  height: 230px;

  margin: 0 auto;
  max-width: 1600px;

  .header .container {
    display: flex;
    justify-content: space-between;
  }

  .main-nav {
    display: flex;
    align-items: center;
  }

  .header-container {
    margin: 0 auto;
    padding: 0 20px;

    &:not(:last-child) {
      display: flex;
      justify-content: space-between;
    }
  }

  @media screen and (min-width: 320px) {
    .header-container {
      margin: 0 auto;
      padding: 0 20px;
    }
  }

  @media screen and (min-width: 768px) {
    .header-container {
      width: $width-tablet-version;
      padding: 0 70px;
    }
  }

  @media screen and (min-width: 1024px) {
    .header-container {
      width: $width-desktop-version;
    }
  }

  .main-nav {
    display: inline-flex;
    align-items: center;
  }

  .nav-list {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-top: 58px;
  }

  .nav-item {
    position: relative;

    &:not(:first-child) {
      margin-left: 40px;
    }
  }

  .header-film {
    margin-top: 41px;
  }

  .film-icon {
    width: 24px;
    height: 24px;
  }

  .logo-name {
    display: none;
  }

  @media screen and (min-width: 768px) {
    .logo-name {
      display: inline-block;
      font-weight: $font-weight-medium;
      color: #ffffff;
      font-size: 30px;
      line-height: 1.7;
    }
  }

  .nav-link {
    padding: 10px 0 10px 0;
    text-align: center;
    text-transform: uppercase;
    margin-left: auto;

    .nav-link {
      padding: 10px 0 10px 0;
      text-align: center;
      text-transform: uppercase;
      margin-left: auto;

      color: #ffffff;

      font-weight: 500;
      font-size: 14px;
      line-height: 1.14;
      letter-spacing: 0.02em;
    }

    .nav-link.current {
      &::after {
        content: '';
        width: 100%;
        height: 3px;
        background: #ff6b08;
        display: block;
        border-radius: 2px;
        position: absolute;
        opacity: 1;
      }
    }

    font-weight: 500;
    font-size: 14px;
    line-height: 1.14;
    letter-spacing: 0.02em;
  }

  .header-search {
    display: block;
    margin-top: 41px;
    width: 100%;
    max-width: 280px;
    background: none;
    border: none;
    border-bottom: 0.5px solid #ffffff;
  }

  .header-form {
    text-align: center;
  }

  .header-label {
    position: relative;
  }

  .header-search {
    display: inline-block;
    margin: 41px auto 0 auto;
    width: 100%;
    max-width: 280px;
    background: none;
    border: none;
    border-bottom: 0.5px solid #ffffff;
    color: #ffffff;

    &::placeholder {
      color: #ffffff;
      font-size: 14px;
      line-height: 16px;
      font-weight: $font-weight-normal;
      font-style: normal;
    }

    &:focus {
      outline: none;
    }
  }

  @media screen and (min-width: 768px) {
    .header-search {
      max-width: 318px;
    }
  }

  .button-search {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    height: 18px;
    width: 18px;
    background: none;
    border: none;
    position: absolute;
    top: 0px;
    right: -2px;
  }

  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)
    ),
    url('../images/header/mobile/Film-mob@x1.jpg');
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header/mobile/Film-mob@x2.jpg');
  }
  @media screen and (min-width: 768px) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header/tablet/Film-tablet@x1.jpg');
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header/tablet/Film-tablet@x2.jpg');
    }
  }
  @media screen and (min-width: 1024px) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header/desktop/Film-desk@x1.jpg');
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header/desktop/Film-desk@x2.jpg');
    }
  }

  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
